import { Action } from './action';
import { Condition } from './condition/index';

export type Allowed = 'allowed';
export type Blocked = 'blocked';

export enum Privilege {
  Allowed,
  Blocked,
}

export type Create = Privilege;
export type Delete = Privilege;
export type Edit = Privilege;
export type View = Privilege;

export type Rule<T = {}> = {
  actions?: Action[];
  conditions: Condition[];
  description: string;
  name: string;
} & T;
