export enum PipelineStatus {
  Finished = 'Finished',
  Queued = 'Queued',
  Running = 'Running',
  Started = 'Started',
}

export enum PipelineWorkflowStatus {
  Canceled = 'Canceled',
  Completed = 'Completed',
  Failed = 'Failed',
  Running = 'Running',
  Queued = 'Queued',
  Waiting = 'Waiting',
}

export enum PipelineStepStatus {
  Canceled = 'Canceled',
  Completed = 'Completed',
  Failed = 'Failed',
  Running = 'Running',
  Queued = 'Queued',
  Waiting = 'Waiting',
}

export type PipelineStepOutputLine = {
  readonly id: string;
  readonly text: string;
  readonly PipelineStepOutputId: PipelineStepOutput['id'];
};

export type PipelineStepOutput = {
  readonly id: string;
  readonly PipeWorkflowStepId: PipelineWorkflowStep['id'];
};

export type PipelineWorkflowStep = {
  readonly enabled: boolean;
  readonly id: string;
  readonly status: PipelineStepStatus;
  readonly PipeWorkflowId: PipelineWorkflow['id'];
};

export type PipelineWorkflow = {
  readonly description: string;
  readonly enabled: boolean;
  readonly id: string;
  readonly name: string;
  readonly ActivePipelineId: Pipeline['id'];
};

export type Pipeline = {
  readonly id: string;
  readonly name: string;
  readonly description: string;
  readonly status: PipelineStatus;
};
