import { DateTime } from 'luxon';

export enum TaskStatus {
  'Canceled' = 'Canceled',
  'Completed' = 'Completed',
  'Created' = 'Created',
  'Faulted' = 'Faulted',
  'Queued' = 'Queued',
  'Running' = 'Running',
  'Waiting' = 'Waiting',
}

export type Task = {
  readonly created?: DateTime;
  readonly data: Record<string, string> | ReadonlyMap<string, string>;
  readonly name: string;
  readonly id?: string;
  readonly status?: TaskStatus;
};

export type Queue = {
  readonly id: string;
  readonly name: string;
  readonly queued: readonly Task['id'][];
  readonly running: readonly Task['id'][];
  readonly finished: readonly Task['id'][];
};

export type QueueInitializationResult = {
  readonly error: boolean | Error;
};
